/* main */
html {
  font-size: 16px; }

body {
  font-family: "SF-Pro-Display", sans-serif;
  color: #000000; }

.container {
  width: 1330px;
  padding: 0 15px;
  margin: 0 auto;
  max-width: 100%; }

/* components */
.header {
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  right: 0; }
  .header__burger {
    display: none; }
    @media only screen and (max-width: 767px) {
      .header__burger {
        display: flex;
        flex-direction: column;
        position: relative;
        width: 7.5vw;
        height: 2.8125vw; } }
    .header__burger::after {
      content: "";
      position: absolute;
      width: 12.5vw;
      height: 27;
      top: -2.8125vw;
      bottom: -2.8125vw;
      left: -2.5vw;
      right: -2.5vw; }
  .header__line {
    width: 100%;
    height: 0.625vw;
    background-color: #fff; }
    .header__line:first-child {
      margin-bottom: 1.5625vw; }
  .header__wrapper {
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 36px 49px; }
    @media only screen and (max-width: 767px) {
      .header__wrapper {
        padding: 6.875vw 7.5vw; } }
    @media only screen and (min-width: 1600px) {
      .header__wrapper {
        padding: 2.25vw 3.0625vw; } }
  .header__logo-link {
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 30px;
    line-height: 1.2;
    color: #ffffff;
    letter-spacing: 0.03em; }
    @media only screen and (min-width: 1600px) {
      .header__logo-link {
        font-size: 1.875vw; } }
    @media only screen and (max-width: 767px) {
      .header__logo-link {
        font-size: 5.625vw; } }

.navigation {
  padding: 0 0 10px; }
  @media only screen and (min-width: 1600px) {
    .navigation {
      padding: 0 0 0.625vw; } }
  @media only screen and (max-width: 1079px) {
    .navigation {
      padding: 0; } }
  @media only screen and (max-width: 767px) {
    .navigation {
      position: absolute;
      right: -150%;
      top: 0;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      height: 100vh;
      width: 100%;
      background-color: #ffffff;
      transition: 0.3s linear;
      padding: 7.5vw 7.5vw 9.6875vw; } }
  .navigation.is-active {
    right: 0; }
  .navigation .navigation__mobile-heading,
  .navigation .navigation__mobile-footer {
    display: none; }
    @media only screen and (max-width: 767px) {
      .navigation .navigation__mobile-heading,
      .navigation .navigation__mobile-footer {
        display: flex;
        align-items: center;
        justify-content: space-between; } }
  .navigation__list {
    display: flex; }
    @media only screen and (max-width: 767px) {
      .navigation__list {
        flex-direction: column;
        padding-bottom: 37px !important; } }
  .navigation__item:not(:last-child) {
    margin-right: 2.5vw; }
    @media only screen and (max-width: 767px) {
      .navigation__item:not(:last-child) {
        margin-right: 0; } }
  @media only screen and (max-width: 767px) {
    .navigation__item:not(:first-child) {
      margin-top: 20px; } }
  .navigation__link {
    text-decoration: none;
    color: #ffffff;
    font-size: 13px;
    text-transform: uppercase;
    font-weight: 500;
    transition: color 0.3s;
    letter-spacing: 0.03em; }
    @media only screen and (max-width: 767px) {
      .navigation__link {
        color: #000000;
        text-transform: capitalize;
        font-size: 8.75vw;
        line-height: 1.17857;
        font-weight: 600; } }
    @media only screen and (min-width: 1600px) {
      .navigation__link {
        font-size: 0.8125vw; } }
    @media (min-width: 1080px) {
      .navigation__link:hover {
        color: #cf3421; } }
    .navigation__link.current {
      color: #cf3421; }
  .navigation__close {
    position: relative;
    width: 4.375vw;
    height: 4.375vw;
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    outline: none;
    background-color: transparent;
    padding: 0; }
    .navigation__close::after {
      content: "";
      position: absolute;
      width: 12.5vw;
      height: 12.5vw;
      left: -4.0625vw;
      top: -4.0625vw;
      right: -4.0625vw;
      bottom: -4.0625vw;
      background-color: transparent; }
    .navigation__close svg {
      height: 4.375vw;
      width: 4.375vw; }
  .navigation__copy {
    font-size: 15px;
    line-height: 18px;
    font-family: "SF-Pro-Text", sans-serif;
    font-weight: 400; }

